<template>
  <div id="products">
    <div class="d-flex justify-content-between align-items-center">
      <h3>Products</h3>
      <router-link
        v-bind:to="{ name: 'product-modify' }"
        class="btn btn-success"
        >Create New Product</router-link
      >
    </div>
    <table class="table my-3" v-if="products.length !== 0">
      <thead>
        <tr>
          <th>ID</th>
          <th>Name</th>
          <th>Orders</th>
          <th class="text-right">Actions</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="product of products" v-bind:key="product.key">
          <td class="font-weight-bold">{{ product.id }}</td>
          <td>{{ product.name }}</td>
          <td>{{ product.orders }}</td>
          <td>
            <router-link
              v-bind:to="{
                name: 'product-details',
                params: { id: product.id },
              }"
              class="d-flex align-items-center justify-content-end"
            >
              <img src="../../assets/icons/ic_eye.svg" alt="" />
            </router-link>
          </td>
        </tr>
      </tbody>
    </table>
    <div v-if="products.length === 0">Records not found</div>
  </div>
</template>

<script>
import { db } from "../../configs/firebaseInit";
export default {
  name: "products",
  data() {
    return {
      products: [],
    };
  },
  created() {
    db.collection("products")
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          const data = {
            key: doc.id,
            id: doc.data().id,
            name: doc.data().name,
            orders: doc.data().orders,
          };
          this.products.push(data);
        });
      });
  },
};
</script>
